.sideNav {
  height: 100%;
  width: 3%;
  background-color: var(--darkest);
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
}

.sideNav div {
  display: flex;
  flex-direction: column;
  font-size: 1.25vw;
  color: var(--lightestDark);
}

.sideNav svg {
  padding: 10px;
  border-radius: 5px;
  transition: 0.25s;
  cursor: pointer;
}

.sideNav svg:hover {
  background-color: var(--purple);
  color: var(--lightestDark);
}

.active {
  background-color: var(--purple);
  color: var(--light);
}

.top {
  margin: auto;
  margin-top: 0;
}

.top img {
  width: 75%;
  margin: auto;
}

.middle {
  margin: auto;
  gap: 50px;
}

.bottom {
  margin: auto;
  margin-bottom: 0;
  gap: 10px;
}

@media only screen and (min-width: 0px) and (max-width: 480px) {
  .sideNav {
    height: 10%;
    width: 100%;
    flex-direction: row;
    padding: 0px;
  }

  .sideNav div {
    display: flex;
    flex-direction: row;
    font-size: 3vh;
    color: var(--light);
  }

  .sideNav svg {
    border-radius: 0px;
  }

  .top {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  .middle {
    margin-right: 5px;
    gap: 5px;
  }

  .bottom {
    margin: auto;
    margin-left: 0;
    gap: 5px;
  }

  .top img {
    width: 3vh;
    margin: auto;
  }

  .active {
    background-color: transparent;
    border-bottom: 2px solid var(--purple);
  }
}
